<template>
<el-container class="layout">
  <el-header height="4.375rem" class="header">
    <div>
      <img :src="logo" class="logoImg"/>
    </div>
  </el-header>
  <el-container>
    <el-aside width="12.5rem" style="height: 100%" class="aside">
      <el-menu
        :unique-opened="true"
        default-active="2"
        class="el-menu-vertical-demo"
        background-color="#16cbc9"
        text-color="#fff"
        active-text-color="#ffd04b">
      <el-submenu :index="item.index" v-for="item in menuList" :key="item.index">
        <template slot="title">
          <span>{{item.title}}</span>
        </template>
        <el-menu-item-group>
          <el-menu-item v-for="subItem in item.subMenuList"
                        :index="subItem.index"
                        :key="subItem.index">{{subItem.subTitle}}</el-menu-item>
        </el-menu-item-group>
      </el-submenu>
    </el-menu>
    </el-aside>
    <el-main>
      <keep-alive>
        <router-view></router-view>
      </keep-alive>
    </el-main>
  </el-container>
</el-container>
</template>

<script>
import logo from '../assets/logo.png'
export default {
  data () {
    return {
      logo,
      menuList: [
        {
          title: '日志',
          index: '1',
          subMenuList: [
            {
              index: '11',
              subTitle: '记录生活'
            },
            {
              index: '12',
              subTitle: '技术点滴'
            }
          ]
        },
        {
          title: '相册',
          index: '2',
          subMenuList: [
            {
              index: '21',
              subTitle: '一些事一些情'
            },
            {
              index: '22',
              subTitle: '生活中的光'
            }
          ]
        }
      ]
    }
  }
}
</script>

<style lang="less">
// @import '../style/theme.less';
@nice-blue:#010202;
.layout {
  .header {
    background-color: @mainColor;
    height: 100px;
  }
  .logoImg {
    width: @logoWidth;
    height: @logoHeight;
    float: left;
  }
  .aside {
    height: 100%;
    background-color: @mainColor;
  }
  .el-header.header {
    padding: 0;
  }
  .el-submenu__title {
    font-size: 1.125rem;
    font-family:Verdana, Geneva, Tahoma, sans-serif
  }

}
</style>



